$redcolor: #901C1C;
$orangecolor: #D3573C;
$bordercolor: #C4C4C4;
$pricecolor: #757575;
$font: calibri;


*{
    font-family: $font;
}

a{
    text-decoration: none;
}

body{
    margin: 0 auto;
    padding: 2%;
}

header{
    display: flex;
    float: left;
    width: 70%;
    padding-top: 0.3em;
}

.logo{
    width: 17em;
    margin-right: 4em;
}

nav{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

#btn_select{
    border: 0.5px solid $bordercolor;
    box-sizing: border-box;
    border-radius: 11px;
    background-color: white;
    color: $redcolor;
    margin-left: 0.5em;
    cursor: pointer;
    font-size: 0.8em;
}

h1{
    color: $redcolor;
    width: 20%;
    margin-left: 74%;
}

section{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
}

article{
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 6em;
}

#photo_graphers{
    height: 17em; 
    width: 17em; 
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}

.format_photos{
    width: 20.5em;
}

.format_photos2{
    width: 38em;
    margin-top: 450px;
    margin-left: 60px;
}

.format_photos3{
    width: 28em;
    margin-left: 60px;
}

.photographers_name{
    color: $orangecolor;
    font-size: 3em;
    margin-top: 0.5em;
    margin-bottom: 0.2em;
    text-align: center;
}

.city{
    color: $redcolor;
}

.prices{
    color: $pricecolor;
    padding-bottom: 0.4em;
}

.city, .slogan, .prices{
    margin: 0.2em;
    font-size: 1.1em;
}

footer{
    height: 10em;
}